body {
  margin: 0;
  font-family: -apple-system, BlinkMacSystemFont, 'Segoe UI', 'Roboto', 'Oxygen',
    'Ubuntu', 'Cantarell', 'Fira Sans', 'Droid Sans', 'Helvetica Neue',
    sans-serif;
  -webkit-font-smoothing: antialiased;
  -moz-osx-font-smoothing: grayscale;
  background: #F5F5DC;
  overflow: hidden;
}
html{overflow: hidden;margin: 0;padding: 0}

code {
  font-family: source-code-pro, Menlo, Monaco, Consolas, 'Courier New',
    monospace;
}

.global-container{
  display: grid;
  justify-content: center;
  align-content: center;
  height: 100vh;
}
.info-process{
  position: fixed;
  top: 10px;
  right: 20px;
  display: grid;
}
.info-process p{
  width: 50px;
  font-size: 32px;
  margin: 0;
  text-align: center;
}
.info-process img{
  width: 50px;
}
.info_button{
  position: fixed;
  bottom: 30px;
  right: 20px;
  width: 50px;
  cursor: pointer;
  opacity: 0.8;
}
.info_button:hover{opacity: 1}
