.blocks-container{
    z-index: 1;
    .wrapper{
        display: flex;
        align-items: center;
        .block{
            width: 200px;
            display: grid;
            align-items: center;
            background: #30C14D99;
            text-align: center;
            border-radius: 10px;
            margin: 1em;
            opacity: 0;
            font-size: 14px;
            .main_value{
                font-size: 24px;
                height: 1.4em;
                color: #fff;
            }
            .answer{
                border-top: 1px solid #fff;
                height: 2.5em;
                display: grid;
                align-content: center;
                user-select: none;
            }
        }
        .block-3{
            width: 300px;
            background: #30C14D99;
            opacity: 1;
            font-size: 22px;
            //animation: animbl3 1s infinite;
            .main_value{
                font-size: 48px;
                //animation: animblH3 1s infinite;
            }
            .answer{
                cursor: pointer;
                &:hover{
                    background: #30C14D77;
                }
            }
        }
        //.block-2{animation: animbl2 1s infinite}
        //.block-2{.main_value{animation: animblH2 1s infinite}}
        //.block-4{animation: animbl4 1s infinite}
        //.block-4{.main_value{animation: animblH4 1s infinite}}
        //.block-5{animation: animbl5 1s infinite}
        //.block-5{.main_value{animation: animblH5 1s infinite}}
        .block-2, .block-4{
            width: 250px;
            background: #30C14D99;
            opacity: 0.5;
            font-size: 18px;
            .main_value{
                font-size: 32px;
            }
        }
    }
}
.fail-window{
    position: fixed;
    top: 0;
    left: 0;
    background: #0009;
    width: 100%;
    height: 100%;
    z-index: 10;
    .container{
        position: absolute;
        display: grid;
        justify-items: center;
        top: 50%;
        left: 50%;
        transform: translate(-50%, -50%);
        background: #eee;
        border-radius: 10px;
        width: 250px;
        height: 200px;
        p{
            margin: 0;
            margin-top: 1em;
            font-size: 32px;
            text-align: center;
        }
        .play{
            cursor: pointer;
            width: 80px;
        }
    }
}
.time-container{
    position: fixed;
    top: 1%;
    left: 50%;
    transform: translateX(-50%);
    p{
        margin: 0;
        font-size: 72px;
    }
}
.info-container{
    position: fixed;
    top: 0;
    left: 0;
    background: #0009;
    width: 100%;
    height: 100%;
    z-index: 20;
    .container{
        position: absolute;
        display: grid;
        justify-items: center;
        top: 50%;
        left: 50%;
        padding: 1em;
        transform: translate(-50%, -50%);
        background: #eee;
        border-radius: 10px;
        width: 300px;
        height: 300px;
        p{
            overflow-y: auto;
        }
    }
}

@keyframes animbl2 {
    0%{
        transform: translateX(0) scale(1);
        opacity: 0.5;
    }
    100%{
        transform: translateX(-103%) scale(0.84);
        opacity: 0;
    }
}
@keyframes animbl3 {
    0%{
        transform: translateX(0) scale(1);
        opacity: 1;
        
    }
    100%{
        transform: translateX(-103%) scale(0.84);
        opacity: 0.5;
    }
}
@keyframes animblH3 {
    0%{font-size: 48px}
    100%{font-size: 32px}
}
@keyframes animbl4 {
    0%{
        transform: translateX(0) scale(1);
        opacity: 0.5;
    }
    100%{
        transform: translateX(-122.5%) scale(1.23);
        opacity: 1;
    }
}
@keyframes animblH4 {
    0%{font-size: 32px}
    100%{font-size: 39px}
}
@keyframes animbl5 {
    0%{
        transform: translateX(0) scale(1);
        opacity: 0;
    }
    100%{
        transform: translateX(-129.5%) scale(1.27);
        opacity: 0.5;
    }
}
@keyframes animblH5 {
    0%{font-size: 24px}
    100%{font-size: 26px}
}